@use 'sass:map';
@use '../abstracts/variables';
@use '../abstracts/functions';
@use '../abstracts/mixins';
@use '../abstracts/placeholders';

/*  >  */
.cart_top_wrap {
	background: #fff;

	.cart_top_inner {
		position: relative;
		width: variables.$defaultInnerWidth;
		margin: 0 auto;
		
		.valid_desc {
			margin-top: 10px;
		}
	}

	.round_green_box {
		border: 1px solid rgba(variables.$kyoboGreen, .3);
	}

	//   
	.title_wrap {
		align-items: flex-start;
		flex-direction: column;
		.title_heading {
			@include mixins.justify_width(center);

			[class^=btn_] {
				margin-left: 6px;
			}
		}

		.info_text {
			margin-top: 10px;
		}

		.right_area {
			position: absolute;
			right: 0;
			top: 10px;

			&.bottom {
				top: auto;
				bottom: 30px;
			}
		}
	}

	.order_type_box, .round_green_box, .util_btn_box, .fold_anchor_box, .valid_check {
		width: 890px;
		box-sizing: border-box;
	}
	.fold_anchor_box {
		margin-bottom: 17px;
	}


	.order_type_box {
		@include mixins.justify_width(center, between);
		margin-bottom: 20px;

		.order_sort_wrap {
			display: inline-block;
			border-radius: 44px;
			box-sizing: border-box;
			vertical-align: top;
			font-size: 0;

			.btn_order_sort {
				display: inline-block;
				position: relative;
				height: 44px;
				padding: 11px 23px 9px;
				@include mixins.font_size_xs;
				color: variables.$textColorGray;
				font-weight: variables.$fontWeightRegular;
				border-top: 1px solid variables.$colorGray02;
				border-bottom: 1px solid variables.$colorGray02;
				box-sizing: border-box;
				vertical-align: top;

				& + .btn_order_sort {
					margin-left: -9px;
				}

				.text {
					display: inline-block;
					position: relative;
					z-index: 2;
					vertical-align: top;
				}

				&:before {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					right: 0;
					top: 0;
					bottom: 0;
					border-radius: 44px;
				}

				&:first-child {
					border-left: 1px solid variables.$colorGray02;
					border-radius: 44px 0 0 44px;

					&:before {
						left: -1px;
					}
				}

				&:last-child {
					border-right: 1px solid variables.$colorGray02;
					border-radius: 0 44px 44px 0;

					&:before {
						right: -1px;
					}
				}

				&.active {
					color: #fff;
					font-weight: variables.$fontWeightBold;

					&:before {
						top: -1px;
						bottom: -1px;
						background-color: variables.$textColorGray;
					}
				}

				&[disabled], &[aria-disabled], &.disabled {
					color: rgba(variables.$textColorGray, .5);
				}
			}
		}
	}

	.util_btn_box {
		@include mixins.justify_width(center, between);
		background: variables.$colorGray06;
		border-radius: 10px;
		padding: 15px 20px 15px 18px;
		margin-bottom: 24px;
	}

	&.sps-blw {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		min-width: variables.$defaultMinWidth;
		box-sizing: border-box;
		z-index: 10;

		&:not(.no_sticky) {
			padding: 20px 0 14px;
			border-bottom: 1px solid variables.$colorGray04;
		}

		.cart_top_inner {
			@include mixins.justify_width(center);
			flex-wrap: wrap;
			margin-bottom: 0;

			.cart_info_wrap {
				top: 162px;
			}

			.title_wrap {
				.right_area {
					position: static;
				}
			}
		}

		.cart_title_box, .order_type_box, .fold_anchor_box {
			flex: 1 1 50%;
		}

		.round_green_box {
			flex: 1 1 100%;
			order: 2;
			margin-top: 30px;
		}

		.cart_title_box .title_heading, .info_text, .util_btn_box, .round_green_box.direct_delivery, .right_area.direct_delivery, .valid_check {
			display: none;
		}

		.cart_title_box {
			order: 2;

			.title_wrap.title_size_lg {
				padding-bottom: 0;
			}
		}

		.order_type_box {
			margin-bottom: 0;
			order: 1;
			-webkit-box-pack: start;
			-ms-flex-pack: start;
			justify-content: flex-start;

			.locate_btn_box {
				.btn_line_gray {
					margin-left: 5px;
					border: 0;

					&:hover, &:focus, &:active {
						background: #fff;
					}
				}
			}
		}

		.fold_anchor_box {
			margin-bottom: 0;
			margin-top: 17px;
			order: 3;
		}

		~ .cart_body {
			.cart_info_wrap {
				position: fixed;
				left: 0;
				right: auto;

				&.reach_bottom {
					position: absolute;
					left: auto !important;
					right: 0;
					top: auto !important;
					bottom: 0;
					transform: none !important;
				}
			}
		}
	}

	&:not(.sps-blw) {
		~ .cart_body {
			.cart_info_wrap {
				transform: none !important;
			}
		}
	}
}
.cart_body {
	position: relative;
	min-height: calc(100vh - 80px);
	z-index: 1;
	.cart_body_inner {
		width: 890px;
	}
	.observer_target {
		position: absolute;
		bottom: 0;
	}

	.cart_info_wrap {
		position: absolute;
		right: 0;
		top: 0;
	}

	.no_data {
		padding: 60px 0;
		border-top: 1px solid variables.$colorGray03;
		border-bottom: 1px solid variables.$colorGray04;

		.btn_wrap {
			margin-top: 12px;
		}
	}

	.title_wrap {

		&.first {
			padding-top: 50px;
		}
	}

	.tbl_row_wrap {
		& ~ .tbl_row_wrap {
			border-top: 0;
		}
		.tbl_row, .tbl_row_line {
			td {
				.address_item {
					.address_name .name,
					.address_person .name,
					.address_person .phone_number,
					.address, .msg {
						@include mixins.font_size_sm;
					}

					.address_name {
						& + .address_person {
							margin-top: 8px;
						}

					}

					.address, .msg {
						margin-top: 8px;
					}
				}
			}
		}
	}

	//    
	.prod_list {
		&.type_order {
			.prod_item {
				width: map.get(variables.$prodProps, 'thumb-v-sm');
				margin-left: 20px;

				&:nth-child(6n+1) {
					margin-left: 0;
				}

				&:nth-child(n+7) {
					margin-top: 20px;
				}
			}

			& + .btn_wrap {
				margin-top: 24px;
			}
		}

	}

	// cart_body    (    .has_data   )
	.tag_wrap.type_line_white {
		.tag {
			min-width: 36px;
			height: 36px;
			padding: 7px 5px 8px;
			text-align: center;

			&.has_data {
				padding: 7px 16px 8px;
			}
		}
	}
}

//   
.cart_info_wrap {
	width: 273px;

	.btn_wrap {
		margin-top: 6px;
	}

	.forms {
		margin-top: 20px;
	}

	.info_text_box {
		margin-top: 30px;

		.text {
			@include mixins.font_size_xxs;
			color: variables.$textColorGray;
		}
	}

	.marketing_banner_box {
		position: relative;
		margin-top: 38px;

		.btn_cart_marketing {
			width: 100%;
			height: 48px;
			color: variables.$kyoboGreen;
			border: 1px solid variables.$kyoboGreen;
			border-radius: 24px;
			box-shadow: 0 5px 10px rgba(variables.$textColorBlack, .1);
		}

		.btn_marketing_close {
			position: absolute;
			width: 20px;
			height: 20px;
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/btn_marketing_close', 'png', 20px, 20px);
			top: -8px;
			right: 0;
		}
	}
}
.payments_info_area {
	margin-bottom: 10px;
	padding: 24px;
	border: 1px solid variables.$colorGray02;
	border-radius: 10px;

	.payments_info_box {
		.payments_title {
			line-height: 0;
			font-size: 0;
			visibility: hidden;
			/*@include mixins.font_size_xs;*/
			/*margin-bottom: 23px;*/
			/*font-weight: variables.$fontWeightBold;*/
		}

		~ .payments_info_box {
			margin-top: 12px;
			padding-top: 20px;
			border-top: 1px solid variables.$colorGray04;
		}
		
		+ .form_chk {
			margin-top: 20px;
			margin-left: -10px;
			margin-right: -10px;
			
			+ .btn_wrap {
				margin-top: 20px;
				+ .btn_wrap {
					margin-top: 6px;
				}
			}
		}
		
		+ .btn_wrap {
			margin-top: 30px;
			
			+ .btn_wrap {
				margin-top: 6px;
			}
		}
	}
	.btn_wrap {
		margin-left: -10px;
		margin-right: -10px;
		
		&:last-child {
			margin-bottom: -10px;
		}
		
	}

	.payments_info_list {
		.payments_info_item {
			@include mixins.justify_width(center);

			.btn_info_popup {
				flex-shrink: 0;
				margin-left: 6px;
			}

			.label {
				flex-shrink: 0;
				@include mixins.font_size_xs;
			}

			.right_box {
				margin-left: auto;
				padding-left: 10px;
			}

			.price {
				display: inline-block;
				font-size: 0;
				vertical-align: top;

				.unit, .val {
					@include mixins.font_size_xs;
				}
				.unit {
					margin-left: 2px;
				}

				.val {
					&.spot {
						font-weight: variables.$fontWeightBold;
						@include mixins.font_size_sm;
					}
					&.sale {
						font-weight: variables.$fontWeightBold;
						@include mixins.font_size_sm;
						color: variables.$kyoboPurple;
					}
					
				}
			}

			&.total {
				align-items: flex-end;

				.label {
					font-weight: variables.$fontWeightBold;
				}

				.price {
					.unit {
						@include mixins.font_size_xs;
						vertical-align: 1px;
					}

					.val {
						font-weight: variables.$fontWeightBold;
						@include mixins.font_size_md;
					}
				}

				~ .point {
					margin-top: 10px;
				}
			}

			&.point {
				color: variables.$textColorLightGray;

				.label {
					@include mixins.font_size_xxs;
				}

				.price {
					.unit, .val {
						@include mixins.font_size_xs;
					}
				}
			}

			~ .payments_info_item {
				margin-top: 12px;
			}
		}
	}
}

//   
.point_banner_wrap {
	margin: 50px 0;

	.swiper_control_box {
		margin-top: 20px;

		.swiper-pagination-bullet {
			width: 6px;
			height: 6px;
			margin: 0 2px;
			background-color: variables.$colorGray02;
		}

		.swiper-pagination-bullet-active {
			background-color: variables.$textColorDarkGray;
		}
	}
}
.point_banner_box {
	@include mixins.justify_width(center, start);
	height: 82px;
	padding: 0 15px 0 24px;
	border: 1px solid variables.$colorGray04;
	border-radius: 10px;

	img {
		margin-left: auto;
	}

	&.bg_gray {
		background-color: #f4f4f4;
	}

	&.bg_indigo {
		color: #fff;
		background: #22406f;
	}
}
.point_banner_text {
	.title {
		font-weight: variables.$fontWeightBold;
	}
}

.gift_prod_list_wrap {
	padding: 20px 0;
	border-top: 1px solid variables.$textColorBlack;
	border-bottom: 1px solid variables.$colorGray04;


}

//   
.get_box_list {
	@include mixins.justify_width;

	li {
		margin-left: 12px;
		flex: 1;

		&:first-child {
			margin-left: 0;
		}
	}

	& + .bul_item_dot {
		margin-top: 16px;
	}
}
.get_box {
	position: relative;
	@include mixins.justify_width(center, center, column);
	flex: 1;
	width: 100%;
	height: 170px;
	border: 1px solid variables.$colorGray02;
	border-radius: 16px;
	cursor: pointer;
	@include mixins.transition(border-color .23s);

	.get_title {
		margin-top: 14px;
		@include mixins.font_size_sm;
	}

	.bubble_speech_desc {
		right: 0;
		top: 0;
		z-index: 1;
		@include mixins.transform(translateY(-50%));
		@include mixins.transition(opacity .23s);
		opacity: 0;
	}

	&:hover,
	&:focus,
	&.active {
		border-color: variables.$textColorBlack;

		.get_title {
			font-weight: variables.$fontWeightBold;
		}
	}

	&.active {
		.bubble_speech_desc {
			opacity: 1;
		}
	}
}

//   
.oder_info_list {
	display: flex;
	margin: 30px 0;

	&:first-child {
		margin-top: 0;
	}

	&:last-child {
		margin-bottom: 0;
	}

	.order_info {
		position: relative;
		flex: 1;

		.title {
			margin-bottom: 13px;
			@include mixins.font_size_sm;
			font-weight: variables.$fontWeightBold;
		}

		.list {
			li {
				position: relative;
				padding-left: 18px;

				& + li {
					margin-top: 8px;
				}

				&:before {
					content: '';
					position: absolute;
					left: 0;
					top: 3px;
					width: 14px;
					height: 14px;
					background-repeat: no-repeat;
					background-position: center center;
				}
			}
		}

		&:not(:first-child) {
			padding-left: 24px;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 10px;
				bottom: 10px;
				border-left: 1px dashed variables.$colorGray03;
			}
		}

		&.order_impossible {
			.title {
				color: variables.$textColorRed;
			}

			.list li:before {
				@include mixins.ratio_images('../images/ico_imposibble_red', 'png', 12px, 12px);
			}
		}

		&.order_possible {
			.title {
				color: variables.$textColorGreen;
			}

			.list li:before {
				@include mixins.ratio_images('../images/ico_posibble_green', 'png', 14px, 14px);
			}
		}
	}
}

/*  */
.sel_gift_card_wrap {
	.gift_card_list {
		.gift_card_item {
			width: calc((100% - 28px) / 3);

			& ~ .gift_card_item {
				margin-left: 14px;
			}

			.img_box {
				position: relative;

				&:after {
					content: '';
					display: block;
					position: absolute;
					left: 0;
					top: 0;
					width: 100%;
					height: 100%;
					border: 2px solid variables.$kyoboPurple;
					border-radius: 12px;
					box-sizing: border-box;
					opacity: 0;
					@include mixins.transition(opacity 0.2s ease-out);
				}
			}

			.info_box {
				.gift_card_name {
					@include mixins.transition(color 0.2s ease-out, font-weight 0.2s ease-out);
				}
			}

			&.active {
				.img_box {
					&:after {
						opacity: 1;
					}
				}
				.info_box {
					.gift_card_name {
						color: variables.$textColorPurple;
						font-weight: variables.$fontWeightBold;
					}
				}
			}
		}
	}
}

.gift_msg_wrap {
	display: block;

	& + .tbl_row_wrap {
		margin-top: 50px;
	}
}
.recipient_info_wrap {
	display: block;

	.recipient_header_area {
		padding: 20px 0;
		margin-top: 10px;

		.recipient_header_inner {
			@include mixins.justify_width;
		}

		.right_area {
			flex-shrink: 0;
			margin-left: auto;
			margin-top: 9px;
			padding-left: 20px;
		}

		&.sps-blw {
			.recipient_header_box {
				position: fixed;
				margin-top: 0;
				padding: 20px 0 14px;
				background: #fff;
				border-bottom: 1px solid variables.$colorGray04;
				left: 0;
				top: 0;
				right: 0;
				z-index: 2;
				.recipient_header_inner {
					width: variables.$defaultInnerWidth;
					margin: 0 auto;
				}
			}
		}

		& + .recipient_contents_area {
			margin-top: 0;
		}
	}
	.recipient_contents_area {
		display: block;
		margin-top: 30px;
		border: 1px solid variables.$colorGray04;
		border-radius: 10px;
		overflow: hidden;

		.recipient_list_wrap {
			.recipient_item {
				display: block;
				position: relative;
				box-sizing: border-box;

				.tbl_row_wrap {
					padding: 0;
					border: none;

					th {
						padding-left: 0;
					}
					td {
						padding-right: 0;
					}
				}

				.btn_delete_ord {
					display: block;
					position: absolute;
					top: -1px;
					right: -13px;
					width: 21px;
					height: 21px;
					padding: 3px;
					@include mixins.ratio_images('../images/btn_delete_ord', 'png', 15px, 15px);
					background: {
						repeat: no-repeat;
						position: center;
					}
				}
			}

			.recipient_list {
				display: block;
				padding: 17px 29px 18px;

				.recipient_item {
					& ~ .recipient_item {
						margin-top: 20px;
						padding-top: 20px;
						border-top: 1px solid variables.$colorGray04;

						.btn_delete_ord {
							top: 17px;
						}
					}
				}
			}

			& ~ .recipient_prod_wrap {
				border-top: 1px solid variables.$colorGray04;
			}
		}

		.recipient_prod_wrap {
			display: block;
			box-sizing: border-box;

			.recipient_prod_contents {
				display: block;
				box-sizing: border-box;

				.fold_box_wrap.type_gray {
					.fold_box {
						border-radius: 0;
						border: 0;

						.fold_box_header {
							padding: 10px 29px;
							color: variables.$textColorBlack;
							@include mixins.font_size_min;

							&:after {
								right: 23px;
							}
						}
					}
				}
			}
			.recipient_prod_footer {
				display: block;
				border-top: 1px solid variables.$colorGray04;
				box-sizing: border-box;
			}
		}
	}
}

/*    */
.country_select_list {
	.country_item {

		& ~ .country_item {
			margin-top: 8px;
		}

		.btn_text_link {
			.text {
				text-decoration: none;
			}

			&:hover, &:focus, &:active {
				.text {
					text-decoration: underline;
				}
			}
		}
	}
}

/*  (, e )   */
.benefit_summary {
	@include mixins.justify_width(center);
	font-size: 0;

	.form_chk, .form_rdo {
		& + .price {
			margin-left: 10px;
		}
	}

	.desc {
		@include mixins.font_size_xs;
		color: variables.$textColorBlack;
		font-weight: variables.$fontWeightMedium;
	}

	.price {
		display: inline-block;
		font-size: 0;
		vertical-align: top;

		.val {
			display: inline-block;
			margin-right: 2px;
			@include mixins.font_size_def;
			color: variables.$textColorPurple;
			font-weight: variables.$fontWeightBold;
			vertical-align: top;
		}

		.unit {
			display: inline-block;
			padding-top: 1px;
			@include mixins.font_size_xs;
			color: variables.$textColorBlack;
			vertical-align: top;
		}
	}

	.right_area {
		margin-left: auto;
	}
}

/*     */
.point_conversion_info_wrap {
	display: block;
	padding-bottom: 20px;
	border-bottom: 1px solid variables.$colorGray04;
	box-sizing: border-box;
	word-break: break-all;

	.point_conversion_title {
		display: block;
		margin-bottom: 24px;
		@include mixins.font_size_sm;
		color: variables.$textColorGray;
		text-align: center;
	}

	.img_box {
		display: block;
		margin-bottom: 30px;

		img {
			width: 100%;
		}
	}

	.payment_price_wrap {
		.payment_price_list {
			.payment_price_item {
				padding: 0 4px;

				.label {
					font-weight: variables.$fontWeightRegular;
				}

				.right_box {
					.price {
						.val {
							margin-right: 1px;
							@include mixins.font_size_def;
						}
						.unit {
							padding-top: 1px;
						}
					}
				}
			}
		}

		.payment_price_total {
			padding-left: 4px;
