.prod_choice_head {
	position: relative;
	padding: 20px 10px;

	.title {
		position: relative;
		padding-right: 16px;
		@include mixins.font_size_sm;
		font-weight: variables.$fontWeightBold;
		cursor: pointer;

		&:after {
			content: '';
			position: absolute;
			right: 0;
			top: 8px;
			width: 10px;
			height: 5px;
			@include mixins.ratio_images('../images/arw_fold_toggle_black', 'png', 10px, 5px);
		}
	}

	&.active {
		.title {
			&:after {
				transform: rotate(-180deg);
			}
		}
		.info_text_list {
			display: block;
		}
	}

	.info_text_list {
		display: none;
		margin-top: 10px;
	}

	.btn_delete_ord {
		position: absolute;
		top: 20px;
		right: 10px;
		width: 21px;
		height: 21px;
		padding: 3px;
		background: {
			repeat: no-repeat;
			position: center center;
		}
		@include mixins.ratio_images('../images/btn_delete_ord', 'png', 15px, 15px);
	}

}
