//  
a.btn_xl, button.btn_xl {
	@include mixins.justify_width(center, center, row, true);
	height: 60px;
	border-radius: 6px;
	padding: 0 23px 1px;
	font-family: variables.$fontKor;
	font-weight: variables.$fontWeightBold;
	@include mixins.font_size_sm;
	
	//  :  height 20px
	[class^=ico_] {
		margin-right: 5px;
		
		&:last-child {
			margin-right: 0;
			margin-left: 5px;
		}
	}
}

//common new
.ico_location_primary {
	display: inline-block;
	width: 10px;
	height: 20px;
	@include mixins.ratio_images('../images/ico_location_primary', 'png', 10px, 20px);
	vertical-align: top;
}
.ico_check_green {//   
	display: inline-block;
	width: 12px;
	height: 8px;
	@include mixins.ratio_images('../images/ico_pickup_status', 'png', 12px, 8px);
}

// 
.bubble_speech_desc {
	
	&.arw_center_bottom:before {
		bottom: -7px;
		left: 50%;
		@include mixins.transform(translateX(-50%));
		@include mixins.transform(rotate(-180deg));
	}
	
	
}

.badge_md {
	&.flag {
		border-radius: 6px 0 6px 0 ;
	}
}


a.btn_md, button.btn_md {
	
	&.btn_line_primary {
		.ico_download_primary {//payment > packaging
			display: inline-block;
			width: 14px;
			height: 14px;
			margin: {
				top: 2px;
				right: 5px;
			}
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_download_h14_primary', 'png', 14px, 14px);
			vertical-align: top;
		}
		.ico_change_arrow_primary {//payment > packaging
			display: inline-block;
			width: 14px;
			height: 14px;
			margin: {
				top: 2px;
				right: 5px;
			}
			background: {
				position: center;
				repeat: no-repeat;
			}
			@include mixins.ratio_images('../images/ico_change_arrow_h14_primary', 'png', 14px, 14px);
			vertical-align: top;
		}
	}
	
	&.btn_line_gray {
		.ico_plus {
			display: inline-block;
			width: 14px;
			height: 14px;
			margin: {
				top: 2px;
				right: 5px;
			}
			background: {
				position: center;
				repeat: no-repeat;
			}
			font-size: 0;
			@include mixins.ratio_images('../images/ico_subscribe', 'png', 14px, 14px);
		}
	}
	
}


.btn_info_popup {
	.ico_setting_gray {
		display: inline-block;
		width: 16px;
		height: 16px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_setting', 'png', 16px, 16px);
		vertical-align: top;
	}
}

.prod_price {
	.count {
		margin-top: 11px;
		display: block;
		@include mixins.font_size_xxs;
		color: variables.$textColorGray;
	}
}

.prod_list {
	//
	&.type_order_packaging {
		.prod_item {
			width: 142px;
		}
	}
}

.prod_area {
	//  
	&.type_packaging {
		
		.prod_info_box {
			.price_apply {
				margin-top: 4px;
				@include mixins.font_size_min;
			}
		}
		
	}
}

.tbl_row_wrap {
	
	tbody {
		th {
			
			[class^=btn_] {
				
				&.btn_xs {
					margin-top: -4px;
					margin-left: 8px;
					
				}
			}
		}
		
		td {
			&.vertical_middle {
				vertical-align: middle;
			}
		}
	}
}
