$paymentPurple: #2B1DD6;
$paymentPurple2: #474C98;
$paymentBlue: #0057BE;

.fc_payment_blue {
	color: $paymentBlue;
}


.badge_payment_line_purple {
	color: $paymentPurple2;
	border: 1px solid $paymentPurple2;
	background: #fff;
}
.badge_payment_line_blue {
	color: $paymentBlue;
	border: 1px solid $paymentBlue;
	background: #fff;
}
.badge_payment_line_black {
	color: variables.$textColorBlack;
	border: 1px solid variables.$textColorBlack;
	background: #fff;
}
.badge_light_green {
	background: rgba(variables.$kyoboGreen, .06);
	border: solid 1px rgba(variables.$kyoboGreen, .06);
	color: variables.$kyoboGreen;
}

.badge_payment_employees {
	height: 25px;
	border-radius: 12.5px;
	padding: 0 12px;
	
	.text {
		@include mixins.font_size_xs;
	}
}
.number_value_box {
	color: #000;
	text-align: right;
	
	.label {
		@include mixins.font_size_xs;
	}
	.point {
		font-size: 0;
		line-height: 0;
		.val {
			font-weight: variables.$fontWeightBold;
		}
		.unit {
			margin-left: 2px;
		}
	}
	
	&.size_md {
		.val,
		.unit {
			@include mixins.font_size_md;
		}
		
	}
	&.size_def {
		.val,
		.unit {
			@include mixins.font_size_def;
		}
		
		.point {
			.unit {
				font-weight: variables.$fontWeightRegular;
				@include mixins.font_size_xs;
			}
		}
		
	}
}


//  +  
.form_info_single {
	position: relative;
	font-size: 0;
	line-height: 0;
	
	.default_text{
		@include mixins.font_size_xs;
		color: variables.$textColorGray;
		
		&.person_info {
			color: variables.$textColorBlack;
			font-weight: variables.$fontWeightMedium;
		}
		&.fc_black {
			color: variables.$textColorBlack;
		}
		
		.bar_text {
			position: relative;
			padding-left: 10px;
			padding-right: 10px;
			
			&:first-child {
				padding-left: 0;
			}
			&:last-child {
				padding-right: 0;
			}
			
			+ .bar_text {
				&:before {
					content: '';
					position: absolute;
					display: inline-block;
					width: 1px;
					height: 9px;
					left: 0;
					top: 50%;
					@include mixins.transform(translateY(-50%));
					background-color: variables.$colorGray03;
					
				}
			}
		}
		
		+.text_sub_spot {
			margin-top: 8px;
		}
		
	}
	
	.text_sub_spot {
		@include mixins.font_size_xxs;
		color: variables.$kyoboPurple;
	}
	
	.number_value_box{
		display: inline-block;
	}
	
	.btn_xs {
		margin-top: -4px;
		margin-bottom: -4px;
		margin-left: 12px;
	}
	
	.bubble_speech_desc {
		&.arw_left_top {
			top: calc(100% + 13px);
		}
	}
}


// text
.quotes_status_box {
	text-align: center;
	
	+.bottom_btn_area {
		margin-top: 20px;
		
		
		.btn_md {
			&.auto {
				min-width: auto;
			}
		}
	}
}
// text
.quotes_status_text {
	display: inline-block;
	position: relative;
	padding: 0 18px;
	color: variables.$textColorGray;
	@include mixins.font_size_xs;
	
	&:before, &:after {
		content: '';
		display: block;
		position: absolute;
		width: 10px;
		height: 9px;
		background: {
			position: center;
			repeat: no-repeat;
		}
		@include mixins.ratio_images('../images/ico_quotes_gray', 'png', 10px, 9px);
	}
	
	&:before {
		top: 3px;
		left: 0;
	}
	
	&:after {
		top: 2px;
		right: 0;
		@include mixins.transform(rotate(180deg));
	}
}





