	}
	
	.card_shape_wrap {
		display: block;
		text-align: center;
		
		.card_shape_area {
			display: inline-block;
			width: 352px;
			padding: 34px 23px 23px;
			background: {
				color: variables.$colorGray06;
				position: calc(100% - 24px) 20px;
				repeat: no-repeat;
			};
			@include mixins.ratio_images('../images/img_card_logo', 'png', 62px, 24px);
			border: 1px solid variables.$colorGray04;
			border-radius: 16px;
			vertical-align: top;
			box-sizing: border-box;
		}
	}
	
	.gift_card_bottom {
		display: block;
		padding: 24px 20px 0;
		
		.tbl_row_wrap {
			&.no_line {
				td {
					.price {
						margin-right: 98px;
					}
				}
			}
		}
	}
	
	.no_data {
		&.size_sm {
			padding: 36px 0;
		}
	}
	
	& + .info_text_box {
		margin-top: 20px;
	}
}
